import React, { useState } from "react";
import "../Styles/style.css";
import { Button, TextField } from "@mui/material";
import { BASE_API_URL_Web } from "../API/api";

function Demo() {
  const [Value, setValue] = useState("");

  const handlesave = async () => {
    try {
      // Replace 'example.jpg' with the desired file name you want to request from the API.
      const fileName = 'beaker.png';
  
      // Make a GET request to the API endpoint to get the image by file name.
      const response = await fetch(
        `${BASE_API_URL_Web}files?fileName=${fileName}`
      );
  
      if (response.ok) {
        const blob = await response.blob();
        // Create a URL for the returned blob and use it in your UI.
        var imageUrl = URL.createObjectURL(blob);
        // Use the imageUrl in your UI, e.g., set it as the source of an <img> element.
        document.getElementById("imagePreview").src = imageUrl;
  
      } else {
        console.error("Error getting image: ", response.status);
      }
    } catch (error) {
      console.error("Error getting image: ", error);
      alert("Dynamic Content Load Failed");
    }
  };
  
  return (
    <>
      <h1>Demo</h1>

      <br />
      <TextField
        fullWidth
        id="outlined-basic"
        label="Remarks"
        variant="outlined"
        size="small"
        value={Value}
        onChange={(event) => setValue(event.target.value)}
      />

      <Button onClick={handlesave}> Save</Button>
      <br />

      <img id="imagePreview" alt="Preview"  style={{height:40 , width:40}}/>
    </>
  );
}

export default Demo;
